___CSS_LOADER_EXPORT___.push([module.id, `*{
    margin: 0;
}

#content{
    font-family: 'Open Sans', sans-serif;
    display: flex;
    flex-direction: column;
}

#header{
    display: flex;
    justify-content: center;
    padding: 10px;
    background-color: #151723;
}

.tab{
    padding: 10px;
    margin: 10px;
    color: #fff;
    border: 2px solid rgb(209, 209, 209);
    border-radius: 10px;
}

.tab:hover{
    cursor: pointer;
    transition: 0.5s;
    background-color: orange;
}

/*styles of main page*/
#page-content-main{
    display: flex;
    flex-direction: column;
    text-align: center;
    height: 87vh;
    justify-content: center;
    background: linear-gradient(
        0deg,
        rgba(0,0,0,0.5),
        rgba(0,0,0,0.5)
    ),url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}

#title-main{
    font-size: 40px;
    margin-bottom: 20px;
    font-weight: bold;
    color: rgb(244, 210, 72);
}

#parrot-main{
    font-size: 20px;
    color: #fff;
}

/* Menu page */

#page-content-menu{
    display: flex;
    flex-direction: column;
}

#title-menu{
    text-align: center;
    margin-bottom: 10px;
    padding: 10px;
}

#gallery{
    display: flex;
    height: 100%;
}

#fig1, #fig2, #fig3, #fig4{
    margin: 10px;
    padding: 10px;
    flex: 1;
    text-align: center;
}

#img1{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    width: 100%;
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    box-shadow: 5px 5px 10px #616161;
}

#img1:hover{
    transition: 0.5s;
    transform: scale(1.1);
}

#img2{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    width: 100%;
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    box-shadow: 5px 5px 10px #616161;
}

#img2:hover{
    transition: 0.5s;
    transform: scale(1.1);
}

#img3{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    width: 100%;
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    box-shadow: 5px 5px 10px #616161;
}

#img3:hover{
    transition: 0.5s;
    transform: scale(1.1);
}

#img4{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    width: 100%;
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    box-shadow: 5px 5px 10px #616161;
}

#img4:hover{
    transition: 0.5s;
    transform: scale(1.1);
}

caption{
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    padding: 10px;
}

/*Contact*/

#page-content-contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#title-contact{
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

.contact-form{
    display: flex;
    width: 350px;
    flex: 1;
    flex-direction: column;
    padding: 10px;
    justify-content: center;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 5px 5px 10px #616161;
}

#divInput1, #divInput2, #divInput3{
    display: flex;
    flex-direction: column;
}

label{
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: bold;
}

input{
    margin-bottom: 10px;
    padding: 10px;
}

#submitContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

button{
    padding: 10px;
    width: 140px;
    color: #fff;
    font-size: 20px;
    background-color: rgb(0, 162, 0);
    border: none;
}

/*Responsive*/

@media only screen and (max-width: 400px) {
    #gallery{
        display: flex;
        flex-direction: column;
    }
}

@media only screen and (min-width: 401px) and (max-width: 960px) {
    #gallery {
        display: flex;
        flex-direction: column;
    }
